import React, { Component } from "react";

import "./ReportPage.css";

import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import Button from "../../components/Buttons/index.js";

import CopyToClipboard from "../../utils/clipboard.js";

import Icon from '../../components/Icon/Icon.js';

import githubIcon from '../../img/github.svg';
import { Important } from "../../components/TextUtils/TextUtils";


export default class ReportPage extends Component {
  T(en, ko) {
    if (this.props.language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }

  copyToClipboard(url) {
    CopyToClipboard(url);
    alert(this.T("Copied to clipboard", "클립보드에 복사되었습니다."));
  }

  render() {
    return (
      <div className="ReportPage">
        <PageFrame>
          <Content.TitleBar>{this.T("Report Bugs", "버그 제보하기")}</Content.TitleBar>
          <Content.Area>
            <Content.Box>
              <Content.TextArea style={{lineHeight: "200%"}}>
                <Label style={{fontSize: "14pt"}}>
                  {this.T(
                    "We apologize for the inconvenience.",
                    "불편을 끼쳐드려 죄송합니다."
                  )}
                </Label>
                <Label style={{fontSize: "14pt"}}>
                  {this.T(
                    "To fix the bug and improve the program, please inform us about this error using the method below.",
                    "버그의 수정과 프로그램의 발전을 위해, 다음 방법을 통해 저희에게 이 오류에 대해 알려주세요."
                  )}
                </Label>
                <Label style={{fontSize: "14pt"}}>
                  {this.T(
                    "We would be grateful if you could provide detailed information, such as how to reproduce the bug or the circumstances under which it occurred.",
                    "버그를 재현하는 방법 또는 어떤 상황에서 발생했는지 등의 부가 정보를 상세히 제공해 주신다면 감사하겠습니다."
                  )}
                </Label>
                <Label style={{fontSize: "14pt"}}>
                  <Important>
                    {this.T(
                      "Please attach the log file provided by the program.",
                      "프로그램에서 제공된 로그 파일을 꼭 첨부해 주세요."
                    )}
                  </Important>
                </Label>
              </Content.TextArea>
            </Content.Box>
          </Content.Area>
          <Button.LinkButton onClick={() => this.copyToClipboard("twitchlinkhelp@gmail.com")} style={{marginTop: "20px"}} className="reportButton emailButton">
            twitchlinkhelp@gmail.com
          </Button.LinkButton>
          <Button.LinkButton style={{marginTop: "20px"}} className="reportButton githubButton" href="https://github.com/devhotteok/TwitchLink" target="_blank">
            <Icon src={githubIcon} alt="GitHub" style={{marginRight: "10px", filter: "invert(1)"}}/>
            GitHub
          </Button.LinkButton>
        </PageFrame>
      </div>
    );
  }
}